import { html, css } from 'lit';

import SearchBox from '../inputs/SearchBox/search-box.lit.csr.mjs';
import SelectInput from '../inputs/SelectInput/select-input.lit.csr.mjs';
import Title from '../Title/title.lit.csr.mjs';
import Panel from '../Panel/panel.lit.csr.mjs';
import Card from '../Card/card.lit.csr.mjs';
import IaiIcon from '../../IaiIcon/iai-icon.mjs';
import Button from '../Button/button.lit.csr.mjs';

import IaiLitBase from '../../../IaiLitBase.mjs';


export default class Consultation extends IaiLitBase {
    static properties = {
        ...IaiLitBase.properties,
        status: { type: String },
        title: { type: String },
        description: { type: String },
        department: { type: String },
        numResponses: { type: Number },
        numQuestions: { type: Number },
        numThemes: { type: Number },
    }

    static styles = [
        IaiLitBase.styles,
        css`
            iai-silver-consultation .topbar {
                display: flex;
                justify-content: space-between;
                margin-bottom: 1em;
            }
            iai-silver-consultation .topbar .tags {
                display: flex;    
                align-items: center;
                gap: 0.5em;
            }

            iai-silver-consultation .tag {
                padding: 0.3em 0.5em;
            }

            iai-silver-consultation .status {
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 0.8em;
                color: white;
                background: #85d07e;                
                border-radius: 0.5em;
            }
            iai-silver-consultation .department {
                font-size: 0.8em;
            }
        `
    ]

    constructor() {
        super();
        this.contentId = this.generateId();

        // Prop defaults
        this.status = "";
        this.title = "";
        this.description = "";
        this.department = "";
        this.numResponses = 0;
        this.numQuestions = 0;
        this.numThemes = 0;

        this.applyStaticStyles("iai-silver-consultation", Consultation.styles);
    }

    getIconColor = (status) => {
        switch (status) {
            case this.CONSULTATION_STATUSES.open:
                return "#85d07e";
            case this.CONSULTATION_STATUSES.analysing:
                return "#efdb5d";
            case this.CONSULTATION_STATUSES.completed:
                return "#0b8478";
            case this.CONSULTATION_STATUSES.closed:
                return "var(--iai-silver-color-text)";
            default:
                return "#85d07e";
        }
    }

    getTagColor = (status) => {
        switch (status) {
            case this.CONSULTATION_STATUSES.open:
                return "#85d07e";
            case this.CONSULTATION_STATUSES.analysing:
                return "#efdb5d";
            case this.CONSULTATION_STATUSES.completed:
                return "#0b8478";
            case this.CONSULTATION_STATUSES.closed:
                return "#f8f9fa";
            default:
                return "#85d07e";
        }
    }

    render() {
        return html`
            <iai-silver-panel>
                <div slot="content">
                    <div class="topbar">
                        <div class="tags">
                            <iai-icon
                                name="schedule"
                                .color=${this.getIconColor(this.status)}
                            ></iai-icon>

                            <span class="tag status" style=${`background: ${this.getTagColor(this.status)}; color: ${this.status == this.CONSULTATION_STATUSES.closed ? "black" : "white"}`}>
                                ${this.status}
                            </span>

                            <span class="tag" style="border-radius: 0.5em; background: white; border: 1px solid var(--iai-silver-color-mid); padding-inline: 0.5em; font-size: 0.8em; display: flex; justify-content: center; align-items: center;">
                                Transport
                            </span>
                        </div>
                        <div>
                            <iai-silver-button
                                .text=${"View analysis"}
                                .handleClick=${() => console.log("button clicked")}
                            ></iai-silver-button>
                        </div>
                    </div>

                    <iai-silver-title
                        .text=${this.title}
                        .variant=${"secondary"}
                    ></iai-silver-title>

                    <p>
                        ${this.description}
                    </p>
                    
                    <div style="display: flex; gap: 1em;">
                        <div style="display: flex; align-items: center; gap: 0.25em; margin-bottom: 0.5em;">
                            <iai-icon
                                name="calendar_month"
                                color="var(--iai-silver-color-text)"
                            ></iai-icon>
                            <small>
                                4 May 2024 - 8 Aug 2024
                            </small>
                        </div>

                        <div style="display: flex; align-items: center; gap: 0.25em; margin-bottom: 0.5em;">
                            <iai-icon
                                name="group"
                                color="var(--iai-silver-color-text)"
                            ></iai-icon>
                            <small>
                                ${this.numResponses.toLocaleString()} responses
                            </small>
                        </div>

                        <div style="display: flex; align-items: center; gap: 0.25em; margin-bottom: 0.5em;">
                            <iai-icon
                                name="description"
                                color="var(--iai-silver-color-text)"
                            ></iai-icon>
                            <small>
                                ${this.numQuestions} questions
                            </small>
                        </div>

                        <div style="display: flex; align-items: center; gap: 0.25em; margin-bottom: 0.5em;">
                            <iai-icon
                                name="monitoring"
                                color="var(--iai-silver-color-text)"
                            ></iai-icon>
                            <small>
                                ${this.numThemes} themes
                            </small>
                        </div>
                    </div>

                    <small class="department">
                        ${this.department}
                    </small>
                </div>
            </iai-silver-panel>
        `
    }
}
customElements.define("iai-silver-consultation", Consultation);