import { html, css } from 'lit';

import IaiIcon from '../../IaiIcon/iai-icon.mjs';
import IconTile from '../IconTile/icon-tile.lit.csr.mjs';
import Panel from '../Panel/panel.lit.csr.mjs';

import IaiLitBase from '../../../IaiLitBase.mjs';


export default class Card extends IaiLitBase {
    static properties = {
        ...IaiLitBase.properties,
        color: { type: String },
        backgroundColor: { type: String },
        text: { type: String },
        number: { type: Number },
        icon: { type: String },
    }

    static styles = [
        IaiLitBase.styles,
        css`
            iai-silver-card {
                display: block;    
                width: max-content;
            }
            iai-silver-card p {
                margin: 0;
            }
            iai-silver-card .number {
                font-size: 1.3em;
            }
            iai-silver-card .text {
                font-size: 0.8em;
            }
            iai-silver-card div[slot="content"] {
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 1em;
                padding: 1em;
            }
        `
    ]

    constructor() {
        super();
        this.contentId = this.generateId();

        // Prop defaults
        this.color = "white";
        this.backgroundColor = "black";
        this.text = "";
        this.number = 0;
        this.icon = "";

        this.applyStaticStyles("iai-silver-card", Card.styles);
    }

    render() {
        return html`
            <iai-silver-panel .borderColor=${this.color}>
                <div slot="content">
                    <iai-silver-icon-tile
                        .color=${this.color}
                        .backgroundColor=${this.backgroundColor}
                        .name=${this.name}
                    ></iai-silver-icon-tile>
                    <div>
                        <p class="text">${this.text}</p>
                        <p class="number">${this.number}</p>
                    </div>
                </div>
            </iai-silver-panel>
        `
    }
}
customElements.define("iai-silver-card", Card);